@import '../variables/routes';
@import 'hover';
@import 'hover-underline';

@mixin arrow($color: white) {
  padding-right: 18px;
  position: relative;

  &::after {
    @include pseudo;
    background: url(#{$path}/images/arrow-link--#{$color}.svg) no-repeat;
    height: 12px;
    margin-top: -6px;
    right: 4px;
    top: 50%;
    transition: all 0.2s $ease--snappy;
    width: 7px;
  }
}

@mixin arrow-link($color: white) {
  @include arrow($color);
  @include hover-underline;
  display: inline-block;
  font-size: 1.4rem;

  @include hover {
    &::after {
      right: 0;
    }
  }
}
