@import "variables/animation.scss";
@import "variables/colors.scss";
@import "variables/layout.scss";
@import "variables/routes.scss";
@import "variables/typography.scss";
@import "functions/px-to-rem.scss";
@import "functions/unit.scss";
@import "functions/width.scss";
@import "mixins/a11y.scss";
@import "mixins/arrow-link.scss";
@import "mixins/article-heading.scss";
@import "mixins/caption.scss";
@import "mixins/clearfix.scss";
@import "mixins/dropdown-image.scss";
@import "mixins/featured-flag.scss";
@import "mixins/hide-text.scss";
@import "mixins/hover-underline.scss";
@import "mixins/hover.scss";
@import "mixins/null-margins.scss";
@import "mixins/placeholder.scss";
@import "mixins/pseudo.scss";
@import "mixins/reset-button.scss";
@import "mixins/reset-list.scss";
@import "mixins/respond-to.scss";
@import "mixins/sidebar-image.scss";
@import "mixins/smooth-text.scss";

/*
 * snapshot
 */

.country-snapshot {
  @include null-margins;
  @include un-smooth-text;
  background-color: $color--white;
  color: $color--tuatara;
  margin-top: unit(-3) !important;
  padding: unit(1);
  position: relative;

  @include respond-to('medium-large') {
    margin-top: unit(-13) !important;
    padding: unit(2);
  }
}

.country-snapshot__intro {
  border-bottom: 1px solid $border--light;
  font: 1.6rem/1.5 $font-family--serif;
  padding-bottom: unit(1);

  @include respond-to('large') {
    font-size: 1.8rem;
  }
}

.country-snapshot__heading {
  font: 6.4rem/1.17 $font-family--kapra-condensed;
  text-transform: uppercase;

  @include respond-to('large') {
    font-size: 7.2rem;
  }
}

/*
 * related resources
 */

.country-related {
  @include null-margins;
  background-color: $color--cararra;
  color: $color--tuatara;
  font-size: 1.4rem;
  line-height: 1.4;
  padding: unit(2);

  &.-small-screen {
    display: block;
    margin-bottom: unit(-4) !important;
    margin-top: unit(2);

    @include respond-to('medium-large') {
      display: none;
    }
  }

  &.-large-screen {
    display: none;

    @include respond-to('medium-large') {
      display: block;
    }
  }
}

.country-related__list {
  @include reset-list;

  a {
    @include hover-underline;
    color: $color--venice-blue;
    display: block;
    padding: unit(1) 0;
  }

  li {
    border-bottom: 1px solid $border--light;
    margin: 0;

    &:first-child a {
      padding-top: 0;
    }
  }
}

.country-related__more-link {
  margin: 0;
  text-align: right;

  a {
    @include arrow-link('dark-orange');
    color: darken($color--rose-of-sharon, 3);
  }
}

/*
 * achievements
 */

.country-achievements {
  @include null-margins;

  @include respond-to('medium-large') {
    padding: 0 unit(2);
  }
}

/*
 * strategy
 */

.country-strategy.layout-container {
  margin-bottom: unit(2.5);
  margin-top: unit(4);
  position: relative;

  @include respond-to('medium-large') {
    margin-top: unit(1);
    padding: unit(2) !important;
  }

  [class*='heading-'] {
    text-align: center;
  }
}

.country-strategy__list {
  list-style: none;
  margin: unit(2) auto;
  max-width: 90rem;
  padding: 0;

  @include respond-to('medium-large') {
    @include clearfix;
  }

  li {
    border-bottom: 1px solid $border--light;
    margin: 0;
    padding: unit(1) 0;

    &:first-child {
      padding-top: 0;
    }

    &:last-child {
      border-bottom: none;
      padding-bottom: 0;
    }

    @include respond-to('medium-large') {
      width: calc(50% - 50px);

      &:nth-child(even) {
        float: right;
      }

      &:nth-child(odd) {
        float: left;
      }

      &:nth-child(1),
      &:nth-child(2) {
        padding-top: 0;
      }

      &:nth-last-child(1),
      &:nth-last-child(2) {
        border-bottom: none;
        padding-bottom: 0;
      }
    }
  }
}

/*
 * highlights
 */

.country-highlights__list {
  @include reset-list;

  li {
    @include null-margins;

    + li {
      margin-top: unit(2);
    }
  }
}

/*
 * background
 */

.country-background {
  @include null-margins;
  @include smooth-text;
  background-color: $color--jelly-bean;
  color: $color--white;
  margin-top: unit(-4) !important;
  padding: unit(1);
  position: relative;

  @include respond-to('medium-large') {
    padding: unit(2);
  }

  [class*='heading-'] {
    text-align: center;
  }
}

// simple 2-column list
.country-background__list {
  @include clearfix;
  @include reset-list;
  column-count: 2;
  column-gap: unit(1);
  margin-left: auto;
  margin-right: auto;
  max-width: unit(24);

  a {
    @include hover-underline;
    color: $color--white;
  }

  li {
    break-inside: avoid;

    + li {
      margin-top: unit(0.5);
    }
  }

  // very specific media query for breaking 2-col list
  // when sidebar is getting narrow but hadn't gone to
  // small-screen view yet
  @media (min-width: 61.25em) and (max-width: 75em) {
    column-count: auto;

    li + li {
      margin-top: 0;
    }
  }
}

// zones of influence
.country-background__zones {
  @include null-margins;
  padding: 0 0 unit(1);
}

.country-background__zones__map {
  display: block;
  float: none;
  height: auto;
  margin: unit(1) auto;
  max-width: 300px;
  width: 100%;
}

// stats
.country-background__zones {
  + .country-background__stats {
    border-top: 1px solid rgba($color--white, 0.1);
    padding-top: unit(1);
  }
}

.country-background__stats {
  @include null-margins;
  padding-bottom: unit(1);

  .stats-list {
    margin-left: auto;
    margin-right: auto;
    max-width: unit(24);
  }
}

// value chains
.country-background__zones,
.country-background__stats {
  + .country-background__value-chains {
    border-top: 1px solid rgba($color--white, 0.1);
    padding: unit(1) 0 0;
  }
}

.country-background__value-chains {
  @include null-margins;
}

// sidebar image
.country-background__image {
  display: none;
  line-height: 0;
  margin: unit(1) 0 0;
  padding: 0;
  position: relative;

  @include respond-to('medium-large') {
    display: block;
  }

  img {
    height: auto;
    width: 100%;
  }
}
